html {
  font-family: Roboto, Arial, sans-serif;
}

body {
  margin: 0;
}

:root {
  --header-height: 60px;
  --hover-color: #00acdc;
}

a {
  color: inherit;
  text-decoration: none;
}

.header {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.logo {
  object-fit: contain;
  width: 40px;
  height: 40px;
  padding: 10px 0;
}

nav {
  text-transform: uppercase;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  transition: all .15s ease-in-out;
}

.nav_item:not(:last-child) {
  margin-right: 20px;
}

.nav_item:hover {
  color: var(--hover-color);
}

.nav_link {
  height: var(--header-height);
  font-size: 12px;
  font-weight: 500;
  line-height: var(--header-height);
  display: inline-block;
}

.is-active {
  color: var(--hover-color);
  position: relative;
}

.is-active:after {
  content: "";
  background: var(--hover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
